import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';


@Component({
  selector: 'ngx-form-chat',
  templateUrl: './ngx-form-chat.component.html',
  styleUrls: ['./ngx-form-chat.component.css']
})
export class NgxFormChatComponent implements OnInit, AfterViewInit {

  @Input() openChat: any = false;
  @Input() config: any = {};
  @Input() data: any = {};
  @Input() useSession: boolean = true;
  @Input() restartChatWhenOpen: boolean = false;
  @Output() onEndChat = new EventEmitter();

  defaultConfig: any = {
    configuration: {
        compagny: {
            logo: "assets/images/FDT-logo.png",
            name: "Default Company",
            logoWidth: "230px"
        },
        background: "#eee",
        fontSize: "16px",
        chatpanel: {
            autoopen: false,
            restartChatWhenOpen: false,
            useSession: false,
            buttonColor: "#FFA800",
            position: {
                width: "500px",
                bottom: "65px",
                height: "70vh"
            }
        },
        userInput: {
            placeholder: "Your answer here",
            buttoncolor: "#0c2d62",
            color: "#ffffff"
        }
    },
    chatbot: {
        name: "Fatima",
        icon: "assets/images/fatima.png",
        background: "#0c2d62",
        color: "#ffffff"
    },
    user: {
        name: "You",
        icon: "assets/images/user.png",
        background: "#FFA800",
        color: "#ffffff"
    }
  }

  chatbotData: any;

  title = 'nx-form-chat';
  startDate = `${('0' + ((new Date()).getHours())).slice(-2)}:${('0' + ((new Date()).getMinutes() + 1)).slice(-2)}`;

  pushMessageTimer: any;
  isTyping: Boolean = false;
  endChat: Boolean = false;
  currentIndex = 0;
  isEcrit: Boolean = true;
  initialWidth = '0px';
  initialHeight = '0px';

  chatlist: any = [];
  returnJSON: any = {};

  inputValue: string = '';

  constructor() {
  };

  ngOnInit(): void {
    this.startChat();
  }


  startChat() {
    this.initChatConfig();
    this.chatbotData.questions = this.data.questions;
    let _chatInit;
    let _inputValue;
    let useSession = this.chatbotData.configuration && this.chatbotData.configuration.chatpanel && this.chatbotData.configuration.chatpanel.useSession === false ? false : true;
    if (useSession) {
      _chatInit = sessionStorage.getItem('chatInit');
      _inputValue = sessionStorage.getItem('inputValue');
    }
    else {
      if (useSession && !this.config.configuration.chatpanel.restartChatWhenOpen) {
        this.restartChat();
      }
    }
    if (_chatInit && JSON.parse(_chatInit)) {
      this.getState();
      if (!this.endChat) {
        this.continueChat(_inputValue || '');
      }
      setTimeout(() => {
        this.scrollToLastMessage();
      }, 500)
    }
    else {
      this.pushChat('bot');
    }
  }

  initChatConfig() {
    this.chatbotData = this.config ? this.config : this.defaultConfig;
  }

  pushChat(user: string) {
    this.scrollToLastMessage();
    this.chatlist.push({
      owner: user,
      date: `${('0' + ((new Date()).getHours())).slice(-2)}:${('0' + ((new Date()).getMinutes() + 1)).slice(-2)}`,
      messages: []
    })
    this.isTyping = true;
    setTimeout(() => {
      this.chatbotData?.questions && this.chatbotData?.questions[this.currentIndex] && this.chatbotData?.questions[this.currentIndex].messageTexts && this.pushMessage(this.chatbotData?.questions[this.currentIndex].messageTexts, 0);
    }, 1000);
  }

  saveState(inputValue: string) {
    let _inputValue: any = inputValue;
    let _endChat: any = this.endChat;
    if (this.useSession) { 
      sessionStorage.setItem('chatlist', JSON.stringify(this.chatlist));
      sessionStorage.setItem('currentIndex', JSON.stringify(this.currentIndex));
      sessionStorage.setItem('returnJSON', JSON.stringify(this.returnJSON));
      sessionStorage.setItem('inputValue', _inputValue);
      sessionStorage.setItem('endChat', _endChat);
      sessionStorage.setItem('chatInit', JSON.stringify(true));
    }
  }

  getState() {
    let _chatlist: any = sessionStorage.getItem('chatlist');
    let _currentIndex: any = sessionStorage.getItem('currentIndex');
    let _returnJSON: any = sessionStorage.getItem('returnJSON');
    let _endChat: any = sessionStorage.getItem('endChat');
    this.chatlist = JSON.parse(_chatlist);
    this.currentIndex = JSON.parse(_currentIndex);
    this.returnJSON =  JSON.parse(_returnJSON);
    this.endChat = JSON.parse(_endChat);
    setTimeout(() => {
      this.scrollToLastMessage();
    }, 20);
  }

  pushMessage(messages: string, index: number) {
    if (index < messages.length && this.chatlist.length > 0) {
      let message = this.fitVariables(messages[index], this.chatbotData?.questions[this.currentIndex])
      this.chatlist[this.chatlist.length -1].messages.push(message);
      this.scrollToLastMessage();
      this.pushMessageTimer = setTimeout(() => {
        this.pushMessage(messages, index+1);
      }, 1000);
    }
    if (index === messages.length -1) {
      this.isTyping = false;
    }
  }

  fitVariables(message: string, chat: any) {
    if (chat.variables && chat.variables[0]) {
      chat.variables.forEach((variable: any) => {
        message = message.replace(variable.cle, this.returnJSON[variable.referenceCode]).trim().replace(/variable.cle/g, this.returnJSON[variable.referenceCode]).trim();
        message = message.replace(variable.cle, this.returnJSON[variable.referenceCode]);
      });
      return message;
    }
    else {
      return message
    }
  }

  getAnswer(inputValue: string) {
    if (this.handleValidation(inputValue)) {
      this.isEcrit = true;
      this.chatlist.push({
        owner: 'user',
        date: `${('0' + ((new Date()).getHours())).slice(-2)}:${('0' + ((new Date()).getMinutes() + 1)).slice(-2)}`,
        messages: [
          inputValue
        ]
      });
      this.chatbotData?.questions && this.chatbotData?.questions[this.currentIndex] && this.chatbotData?.questions[this.currentIndex].code ? this.returnJSON[this.chatbotData?.questions[this.currentIndex].code] = inputValue : null;
      this.inputValue = "";
      this.saveState(inputValue);
      this.continueChat(inputValue);
    }
    else {
      this.isEcrit = true;
      this.chatlist.push({
        owner: 'user',
        date: `${('0' + ((new Date()).getHours())).slice(-2)}:${('0' + ((new Date()).getMinutes() + 1)).slice(-2)}`,
        messages: [
          inputValue
        ]
      });
      this.inputValue = "";
      this.saveState(inputValue);
      this.sendError(inputValue);
    }
  }

  sendError(inputValue: string) {
    this.scrollToLastMessage();
    this.pushChatError('bot');
    this.isEcrit = this.chatbotData?.questions[this.currentIndex].type === 'text' ? true : false;
  }

  pushChatError(user: string) {
    this.scrollToLastMessage();
    this.chatlist.push({
      owner: user,
      date: `${('0' + ((new Date()).getHours())).slice(-2)}:${('0' + ((new Date()).getMinutes() + 1)).slice(-2)}`,
      messages: []
    })
    this.isTyping = true;
    setTimeout(() => {
      this.chatbotData?.questions && this.chatbotData?.questions[this.currentIndex] && this.chatbotData?.questions[this.currentIndex].validator && this.chatbotData?.questions[this.currentIndex].validator.messages && this.pushMessage(this.chatbotData?.questions[this.currentIndex].validator.messages, 0);
    }, 1000);
  }

  handleValidation(inputValue: string) {
    if (this.chatbotData?.questions && this.chatbotData?.questions[this.currentIndex] && this.chatbotData?.questions[this.currentIndex].validator) {
      let validator = this.chatbotData?.questions[this.currentIndex].validator;
      switch(validator.type) {
        case 'text': 
          if (validator.min && inputValue.length < validator.min) {
            return false;
          }
          else if (validator.max && inputValue.length > validator.max) {
            return false;
          }
          else if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'number':
          let numberRegExp = /^[0-9]*$/;
          if (validator.min && parseFloat(inputValue) < validator.min) {
            return false;
          }
          else if (validator.max && inputValue > validator.max) {
            return false;
          }
          else if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if(!numberRegExp.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'email':
          let emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!emailRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'date':
          let dateRegex = /^(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!dateRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'time':
          let timeRegex = /^(0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!timeRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'datetime':
          let datetimeRegex = /^(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d[- /.](0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!datetimeRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'url':
          let urlRegex = /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)?[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!urlRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'phone':
          let phoneRegex = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!phoneRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'color':
          let colorRegex = /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/g;
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!colorRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        case 'range':
          let rangeRegex = /^[0-9]{1,2}$/g;
          if (validator.min && parseFloat(inputValue) < validator.min) {
            return false;
          }
          else if (validator.max && inputValue > validator.max) {
            return false;
          }
          else if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          else if (!rangeRegex.test(inputValue)) {
            return false;
          }
          else {
            return true;
          }
        // case 'select':
        //   if (validator.regExp && !validator.regExp.test(inputValue)) {
        //     return false;
        //   }
        //   else {
        //     return true;
        //   }
        // case 'radio':
        //   if (validator.regExp && !validator.regExp.test(inputValue)) {
        //     return false;
        //   }
        //   else {
        //     return true;
        //   }
        // case 'checkbox':
        //   if (validator.regExp && !validator.regExp.test(inputValue)) {
        //     return false;
        //   }
        //   else {
        //     return true;
        //   }
        // case 'textarea':
        //   if (validator.min && inputValue.length < validator.min) {
        //     return false;
        //   }
        //   else if (validator.max && inputValue.length > validator.max) {
        //     return false;
        //   }
        //   else if (validator.regExp && !validator.regExp.test(inputValue)) {
        //     return false;
        //   }
        //   else {
        //     return true;
        //   }
        default:
          if (validator.regExp && !validator.regExp.test(inputValue)) {
            return false;
          }
          return true;
      }
    }
    else {
      return true;
    }
  }

  continueChat(inputValue: string) {
    this.currentIndex = this.handleSuivant(inputValue);
    this.scrollToLastMessage();
    if (this.currentIndex >= 0) {
      this.pushChat('bot');
      this.isEcrit = this.chatbotData?.questions[this.currentIndex].type === 'text' ? true : false;
      if (this.chatbotData?.questions[this.currentIndex].type === 'end') {
        this.handleFin();
        this.endChat = true;
        this.saveState(inputValue);
      }
    }
    else {
      this.handleFin();
      this.endChat = true;
      this.saveState(inputValue);
    }
  }

  handleSuivant(inputValue: string) {
    if (this.chatbotData?.questions && this.chatbotData?.questions[this.currentIndex]) {
      if (this.chatbotData?.questions[this.currentIndex].type === 'text') {
        return this.chatbotData?.questions.findIndex((question: any) => question.code === this.chatbotData?.questions[this.currentIndex].next);
      }
      else if (this.chatbotData?.questions[this.currentIndex].type === 'option') {
        let myReponse = this.chatbotData?.questions[this.currentIndex].options.find((reponse: any) => reponse.value === inputValue);
        return myReponse ? this.chatbotData?.questions.findIndex((question: any) => question.code === myReponse.next) : -1;
      }
      else if (this.chatbotData?.questions[this.currentIndex].type === 'end') {
        this.handleFin();
        return -1;
      }
      else {
        this.handleFin();
        return -1;
      }
    }
    this.handleFin();
    return -1;
  }

  handleFin() {
    this.onEndChat.emit(this.returnJSON)
  }

  handleOpenChat(){
    this.openChat = !this.openChat;
    let restartChatWhenOpen = this.chatbotData.configuration && this.chatbotData.configuration.chatpanel && this.chatbotData.configuration.chatpanel.restartChatWhenOpen === true ? true : false;
    if (restartChatWhenOpen) {
      this.restartChat();
      if (!this.openChat) {
        clearTimeout(this.pushMessageTimer);
      }
      else {
        setTimeout(() => {
          this.startChat();
        }, 100)
      }
    }
  }

  restartChat() {
    this.isTyping = false;
    this.endChat = false;
    this.currentIndex = 0;
    this.isEcrit = true;
    this.initialWidth = '0px';
    this.initialHeight = '0px';
  
    this.chatlist = [];
    this.returnJSON = {};
  
    this.inputValue = '';

    sessionStorage.removeItem('chatInit');
    sessionStorage.removeItem('inputValue');
    sessionStorage.removeItem('chatlist');
    sessionStorage.removeItem('currentIndex');
    sessionStorage.removeItem('returnJSON');
    sessionStorage.removeItem('endChat');
  }

  ngAfterViewInit(): void {
    // $('#intiveUsers').modal({
    //     backdrop: 'static',
    //     keyboard: false
    // });
    this.initialWidth = this.chatbotData?.configuration?.chatpanel?.position?.width;
    this.initialHeight = this.chatbotData?.configuration?.chatpanel?.position?.height;
    if(this.chatbotData?.configuration?.chatpanel?.autoopen) {
      document.getElementById('chat-button')?.click();
      this.openChat = true;
    }
    this.handleSize(screen.width, screen.height);
    window.addEventListener('resize', (event: any) => {
    // $(window).resize((event: any) => {
      let height = event.target.innerHeight;
      let width = event.target.innerWidth
      this.handleSize(width, height);
    });
  }

  handleSize(width: number, height: number) {
    if (width < 500) {
      this.chatbotData.configuration.chatpanel.position.width = (width - 50) + 'px';
      this.chatbotData.configuration.chatpanel.position.height = '80vh';
    }
    else {
      this.chatbotData.configuration.chatpanel.position.width = this.initialWidth;
      this.chatbotData.configuration.chatpanel.position.height = this.initialHeight;
    }
  }

  scrollToLastMessage() {
    setTimeout(() => {
      document.getElementById('chat-body')?.scrollTo(0, 60000);
    }, 20);
  }
}
